import React, { useState } from 'react';
import { Coins, Plus, Disc } from 'lucide-react';
import AICard from './AICard';
 import { AIPersona } from '../../types';
import { AI_PERSONAS } from '../constants';
 
const CATEGORIES = ["All", "Relationship", "Career", "Astrology", "Friend", "Casual"];

interface HomeScreenProps {
  coins: number;
  onNavigateToCall: (persona: AIPersona) => void;
}

const HomeScreen: React.FC<HomeScreenProps> = ({ coins, onNavigateToCall }) => {
  const [selectedCategory, setSelectedCategory] = useState("All");

  const filteredPersonas = selectedCategory === "All" 
    ? AI_PERSONAS 
    : AI_PERSONAS.filter(p => p.category === selectedCategory);

  return (
    <div className="max-w-md mx-auto min-h-screen relative pb-12">
      {/* Header */}
      <header className="pt-8 pb-4 px-6 sticky top-0 z-20 bg-[#020617]/90 backdrop-blur-xl border-b border-white/5">
        <div className="flex justify-between items-start mb-1">
          <div>
            <div className="text-xs font-bold tracking-widest text-transparent bg-clip-text bg-gradient-to-r from-gray-400 to-gray-200 uppercase mb-0.5">
              GenzMeet
            </div>
            <h1 className="text-3xl font-bold text-white tracking-tight">
              AI Verse
            </h1>
          </div>
          
          {/* Coin Widget */}
          <div className="glass-card rounded-full px-1 py-1 pl-3 flex items-center gap-2">
            <div className="flex items-center gap-1.5">
               <Coins className="w-4 h-4 text-yellow-400 fill-yellow-400/20" />
               <span className="text-sm font-bold text-white">{coins.toLocaleString()}</span>
            </div>
            <button className="bg-white/10 hover:bg-white/20 text-white text-[10px] font-bold px-3 py-1.5 rounded-full uppercase tracking-wide transition-colors flex items-center gap-1">
              <Plus className="w-3 h-3" /> Get
            </button>
          </div>
        </div>
        <p className="text-gray-400 text-sm font-medium mb-4">
          Pick your AI. Pay with coins. Talk in voice.
        </p>

        {/* Category Chips - Horizontal Scroll */}
        <div className="flex pt-2 gap-2 overflow-x-auto no-scrollbar pb-2 -mx-6 px-6 mask-linear-fade">
          {CATEGORIES.map(category => (
            <button
              key={category}
              onClick={() => setSelectedCategory(category)}
              className={`whitespace-nowrap px-4 py-2 rounded-full text-xs font-bold tracking-wide transition-all duration-300 border backdrop-blur-sm
                ${selectedCategory === category
                  ? 'bg-white/15 border-white/30 text-white shadow-[0_0_15px_rgba(255,255,255,0.1)] scale-105' 
                  : 'bg-white/5 border-white/5 text-gray-500 hover:bg-white/10 hover:text-gray-300'
                }`}
            >
              {category}
            </button>
          ))}
        </div>
      </header>

      {/* AI List */}
      <main className="px-5 mt-4 flex flex-col gap-2 min-h-[50vh]">
        {filteredPersonas.length > 0 ? (
          filteredPersonas.map(persona => (
            <AICard 
              key={persona.id} 
              persona={persona} 
              onCall={onNavigateToCall}
            />
          ))
        ) : (
           // Empty State
           <div className="flex flex-col items-center justify-center py-20 text-center opacity-50">
             <div className="w-16 h-16 rounded-full bg-white/5 flex items-center justify-center mb-4">
                <Disc className="w-8 h-8 text-gray-500" />
             </div>
             <p className="text-gray-400 font-medium">No AIs found for <br/><span className="text-white">"{selectedCategory}"</span></p>
           </div>
        )}

        {/* Spacer for bottom scrolling */}
        <div className="h-12"></div>
      </main>
    </div>
  );
}

export default HomeScreen;