import React, { useState } from 'react';
import HomeScreen from './components/HomeScreen';
import CallScreen from './components/CallScreen';
import { AIPersona } from '../types';
 
export default function App() {
  const [coins, setCoins] = useState(1250);
  const [activePersona, setActivePersona] = useState<AIPersona | null>(null);
  const [currentScreen, setCurrentScreen] = useState<'home' | 'call'>('home');

  const navigateToCall = (persona: AIPersona) => {
    setActivePersona(persona);
    setCurrentScreen('call');
  };

  const navigateHome = () => {
    setCurrentScreen('home');
    // We clear activePersona only after navigating to avoid flicker, or keep it if we want to remember last selection
    // setActivePersona(null); 
  };

  const spendCoins = (amount: number) => {
    setCoins(prev => Math.max(0, prev - amount));
  };

  return (
    <div className="bg-[#020617] min-h-screen text-white font-['Outfit']">
      {currentScreen === 'home' && (
        <HomeScreen 
          coins={coins} 
          onNavigateToCall={navigateToCall} 
        />
      )}
      
      {currentScreen === 'call' && activePersona && (
        <CallScreen 
          persona={activePersona} 
          onBack={navigateHome}
          onSpendCoins={spendCoins}
          coins={coins}
        />
      )}
    </div>
  );
}