import React, { useState, useEffect, useRef } from 'react';
import { ArrowLeft, Mic, MicOff, PhoneOff, Sparkles, Brain, Palette, Heart } from 'lucide-react';
 import { LiveClient } from '../services/liveClient';
import LiveVisualizer from './LiveVisualizer';
import { AIPersona, LiveConnectionState } from '../../types';

interface CallScreenProps {
  persona: AIPersona;
  onBack: () => void;
  onSpendCoins: (amount: number) => void;
  coins: number;
}

const iconMap: Record<string, React.FC<any>> = {
  sparkles: Sparkles,
  brain: Brain,
  palette: Palette,
  heart: Heart,
};

const CallScreen: React.FC<CallScreenProps> = ({ persona, onBack, onSpendCoins, coins }) => {
  const [connectionState, setConnectionState] = useState<LiveConnectionState>({
    isConnected: false,
    isConnecting: false,
    error: null,
    volumeLevel: 0
  });
  
  const liveClientRef = useRef<LiveClient | null>(null);
  const Icon = iconMap[persona.avatarIcon] || Sparkles;

  // Cleanup on unmount
  useEffect(() => {
    return () => {
      if (liveClientRef.current) {
        liveClientRef.current.disconnect();
      }
    };
  }, []);

  const handleToggleConnection = async () => {
    if (connectionState.isConnected || connectionState.isConnecting) {
      await disconnect();
    } else {
      await connect();
    }
  };

  const connect = async () => {
    if (coins < persona.cost) {
      alert("Not enough coins!");
      return;
    }

    setConnectionState(prev => ({ ...prev, isConnecting: true, error: null }));
    
    const client = new LiveClient();
    liveClientRef.current = client;

    await client.connect({
      voiceName: persona.voiceName,
      systemInstruction: persona.systemInstruction,
      onOpen: () => {
        setConnectionState(prev => ({ ...prev, isConnected: true, isConnecting: false }));
        onSpendCoins(persona.cost);
      },
      onClose: () => {
        setConnectionState(prev => ({ ...prev, isConnected: false, isConnecting: false, volumeLevel: 0 }));
      },
      onError: (err) => {
        setConnectionState(prev => ({ ...prev, isConnected: false, isConnecting: false, error: err.message }));
      },
      onVolumeChange: (vol) => {
        setConnectionState(prev => ({ ...prev, volumeLevel: vol }));
      }
    });
  };

  const disconnect = async () => {
    if (liveClientRef.current) {
      await liveClientRef.current.disconnect();
      liveClientRef.current = null;
    }
  };

  const activeColor = persona.themeColor;
  const glowStyle = { '--neon-color': persona.rgbColor } as React.CSSProperties;

  return (
    <div className="fixed inset-0 z-50 bg-[#020617] flex flex-col items-center justify-between py-8 px-6 animate-in fade-in duration-300">
      
      {/* Background Ambience */}
      <div className={`absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-[300px] h-[300px] rounded-full bg-${activeColor}-600/10 blur-[100px] pointer-events-none transition-all duration-1000 ${connectionState.isConnected ? 'scale-150 opacity-50' : 'scale-100 opacity-20'}`} />

      {/* Header */}
      <div className="w-full flex justify-between items-center relative z-10">
        <button 
          onClick={() => { disconnect(); onBack(); }}
          className="p-3 rounded-full bg-white/5 hover:bg-white/10 transition-colors text-white"
        >
          <ArrowLeft className="w-5 h-5" />
        </button>
        <div className="text-center">
           <div className="text-[10px] font-bold text-gray-400 uppercase tracking-widest">Call with</div>
           <div className="text-lg font-bold text-white">{persona.name}</div>
        </div>
        <div className="w-10"></div> {/* Spacer for balance */}
      </div>

      {/* Center Avatar & Visualizer */}
      <div className="flex-1 flex flex-col items-center justify-center relative z-10 w-full">
        
        {/* Avatar Container */}
        <div className="relative mb-8">
            {/* Ripples when talking */}
            {connectionState.isConnected && (
              <>
                 <div className={`absolute inset-0 rounded-full border border-${activeColor}-500/30 animate-[ping_2s_cubic-bezier(0,0,0.2,1)_infinite]`} />
                 <div className={`absolute inset-0 rounded-full border border-${activeColor}-500/20 animate-[ping_2s_cubic-bezier(0,0,0.2,1)_infinite] delay-300`} />
              </>
            )}

            <div 
              className={`w-40 h-40 rounded-full bg-gradient-to-br from-${activeColor}-400/20 to-${activeColor}-900/40 flex items-center justify-center border border-${activeColor}-500/50 shadow-2xl backdrop-blur-md transition-all duration-500 neon-glow`}
              style={glowStyle}
            >
               <Icon className={`w-20 h-20 text-${activeColor}-300 drop-shadow-[0_0_15px_rgba(255,255,255,0.3)]`} />
            </div>
            
            {/* Status Pill */}
            <div className="absolute -bottom-3 left-1/2 -translate-x-1/2">
               <div className={`px-3 py-1 rounded-full text-[10px] font-bold uppercase tracking-wider border backdrop-blur-xl flex items-center gap-2 whitespace-nowrap
                 ${connectionState.isConnected 
                   ? `bg-${activeColor}-950/80 border-${activeColor}-500/50 text-${activeColor}-400` 
                   : 'bg-gray-900/80 border-gray-700 text-gray-400'
                 }`}>
                  <div className={`w-1.5 h-1.5 rounded-full ${connectionState.isConnected ? `bg-${activeColor}-400 animate-pulse` : 'bg-gray-500'}`} />
                  {connectionState.isConnecting ? 'Connecting...' : connectionState.isConnected ? 'Live Audio' : 'Ready'}
               </div>
            </div>
        </div>

        {/* Visualizer */}
        <div className="h-16 flex items-center justify-center w-full max-w-xs">
           {connectionState.isConnected && (
             <div className="flex items-end gap-1.5 h-full">
                {[...Array(5)].map((_, i) => (
                    <div 
                      key={i}
                      className={`w-2 rounded-full bg-${activeColor}-400 transition-all duration-75`}
                      style={{ 
                        height: `${Math.max(15, Math.min(100, connectionState.volumeLevel * (0.5 + Math.random())))}%`,
                        opacity: 0.6 + (connectionState.volumeLevel / 200)
                      }} 
                    />
                ))}
             </div>
           )}
           {connectionState.isConnecting && (
              <p className="text-sm text-gray-500 animate-pulse">Establishing secure frequency...</p>
           )}
        </div>

        <p className="text-gray-400 text-center max-w-[250px] mt-4 text-sm leading-relaxed">
          {connectionState.isConnected 
            ? "Listening... Speak naturally." 
            : `Tap the mic to start a conversation with ${persona.name}.`}
        </p>

      </div>

      {/* Footer Controls */}
      <div className="w-full pb-8 flex items-center justify-center gap-6 relative z-10">
         
         {/* Mute / Secondary (Disabled for demo simplicity, just visual) */}
         <button className="w-12 h-12 rounded-full bg-white/5 border border-white/10 flex items-center justify-center text-gray-400 hover:bg-white/10 transition-colors">
            <MicOff className="w-5 h-5" />
         </button>

         {/* Main Action */}
         <button 
           onClick={handleToggleConnection}
           className={`w-20 h-20 rounded-full flex items-center justify-center transition-all duration-300 transform active:scale-95 shadow-xl
             ${connectionState.isConnected 
               ? 'bg-red-500/20 text-red-400 border border-red-500/50 hover:bg-red-500/30' 
               : `bg-white text-${activeColor}-600 hover:bg-gray-100 border border-white/50 shadow-[0_0_30px_rgba(255,255,255,0.2)]`
             }`}
         >
            {connectionState.isConnected ? (
               <PhoneOff className="w-8 h-8 fill-current" />
            ) : (
               <Mic className="w-8 h-8 fill-current" />
            )}
         </button>

         {/* Settings / Secondary */}
         <button className="w-12 h-12 rounded-full bg-white/5 border border-white/10 flex items-center justify-center text-gray-400 hover:bg-white/10 transition-colors">
            <div className="w-1 h-1 bg-current rounded-full mx-0.5"></div>
            <div className="w-1 h-1 bg-current rounded-full mx-0.5"></div>
            <div className="w-1 h-1 bg-current rounded-full mx-0.5"></div>
         </button>

      </div>
    </div>
  );
};

export default CallScreen;