import React from 'react';
import { Sparkles, Brain, Palette, Heart, Phone, Coins } from 'lucide-react';
import { AIPersona } from '../../types';
  
interface AICardProps {
  persona: AIPersona;
  onCall: (persona: AIPersona) => void;
}

const iconMap: Record<string, React.FC<any>> = {
  sparkles: Sparkles,
  brain: Brain,
  palette: Palette,
  heart: Heart,
};

const AICard: React.FC<AICardProps> = ({ persona, onCall }) => {
  const Icon = iconMap[persona.avatarIcon] || Sparkles;

  // Dynamic style for the glow based on the persona's color
  const glowStyle = {
    '--neon-color': persona.rgbColor,
  } as React.CSSProperties;

  return (
    <div 
      className={`glass-card rounded-[2rem] p-5 w-full mb-4 relative overflow-hidden transition-all duration-500 group hover:bg-white/5 border border-white/5 hover:border-${persona.themeColor}-400/30`}
      style={glowStyle}
    >
      {/* Background ambient glow blob */}
      <div className={`absolute -right-10 -top-10 w-40 h-40 bg-${persona.themeColor}-500/20 rounded-full blur-[50px] pointer-events-none transition-opacity duration-500 opacity-0 group-hover:opacity-20`} />

      <div className="flex flex-row items-center gap-4 relative z-10">
        {/* Avatar */}
        <div className={`flex-shrink-0 w-16 h-16 rounded-full bg-gradient-to-br from-${persona.themeColor}-400/20 to-${persona.themeColor}-600/10 flex items-center justify-center border border-${persona.themeColor}-500/30 shadow-[0_0_15px_rgba(0,0,0,0.3)]`}>
          <Icon className={`w-8 h-8 text-${persona.themeColor}-400 drop-shadow-lg`} />
        </div>

        {/* Content */}
        <div className="flex-1 min-w-0">
          <div className="flex items-center gap-2 mb-1">
             <span className={`px-2 py-0.5 rounded-full text-[10px] font-bold uppercase tracking-wider bg-${persona.themeColor}-500/20 text-${persona.themeColor}-300 border border-${persona.themeColor}-500/20`}>
               {persona.category}
             </span>
          </div>
          <h3 className="text-xl font-bold text-white tracking-tight truncate leading-tight mb-1">
            {persona.name}
          </h3>
          <p className="text-gray-400 text-sm leading-snug truncate">
            {persona.description}
          </p>
        </div>
      </div>

      {/* Footer / Actions */}
      <div className="mt-5 flex items-center justify-between">
        <div className="flex items-center gap-1.5 text-xs font-medium text-gray-400">
          <Coins className="w-3.5 h-3.5 text-yellow-400" />
          <span>{persona.cost}/min</span>
        </div>

        <button
          onClick={() => onCall(persona)}
          className={`relative overflow-hidden pl-5 pr-6 py-2.5 rounded-full flex items-center gap-2 transition-all duration-300 transform active:scale-95 bg-gradient-to-r from-${persona.themeColor}-500 to-${persona.themeColor}-700 text-white shadow-lg shadow-${persona.themeColor}-500/20 hover:shadow-${persona.themeColor}-500/40 border border-white/10`}
        >
           {/* Scanline effect overlay */}
           <div className="absolute inset-0 bg-white/10 w-full h-full skew-x-12 translate-x-[-150%] group-hover:animate-[shimmer_1s_infinite]"></div>
           
           <Phone className="w-4 h-4 fill-current" />
           <span className="text-sm font-semibold tracking-wide">
             Call
           </span>
        </button>
      </div>
    </div>
  );
};

export default AICard;